@import url("https://cdnjs.cloudflare.com/ajax/libs/normalize/8.0.1/normalize.min.css");
* {
    box-sizing: border-box;
}


/* GLOBAL STYLES */

body,
.App {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    font-size: 16px;
    background: url(https://www.toptal.com/designers/subtlepatterns/patterns/lightpaperfibers.png);
}

button {
    background: none;
    border: 0;
    margin: 0;
    padding: 0;
    font-size: inherit;
    font-family: inherit;
    cursor: pointer;
    color: rgb(5, 155, 10);
}

button:hover {
    color: rgb(231, 22, 11);
}

.App {
    display: flex;
}


/* SIDEBAR STYLES */

.app-sidebar {
    width: 25%;
    height: 100vh;
    border-right: 2px solid rgb(152, 56, 56);
}

.app-sidebar-header {
    display: flex;
    justify-content: space-between;
    padding: 20px;
}

.app-sidebar-header h1 {
    margin: 0;
}

.app-sidebar-notes {
    height: calc(100vh - 78px);
    overflow-y: scroll;
}

.app-sidebar-note {
    padding: 25px;
    cursor: pointer;
}

.sidebar-note-title {
    display: flex;
    justify-content: space-between;
}

.app-sidebar-note button {
    color: crimson;
}

.app-sidebar-note p {
    margin: 10px 0;
}

.app-sidebar-note small {
    display: block;
    color: rgb(42, 172, 55);
}

.app-sidebar-note:hover {
    background: rgb(221, 221, 221);
}

.app-sidebar-note.active,
.app-sidebar-note.active small {
    background: rgb(64, 189, 59);
    color: rgb(245, 240, 240);
}


/* NOTE EDITOR/PREVIEW (MAIN) STYLES */

.app-main {
    width: 70%;
    height: 100vh;
}

.app-main-note-edit,
.app-main-note-preview {
    height: 50vh;
}

.no-active-note {
    width: 70%;
    height: 100vh;
    line-height: 100vh;
    text-align: center;
    font-size: 2rem;
    color: rgba(216, 195, 195, 0.459);
}


/* Editing */

.app-main-note-edit {
    padding: 25px;
}

.app-main-note-edit input,
textarea {
    display: block;
    border: 1px solid #ddd;
    margin-bottom: 20px;
    width: 100%;
    height: calc(50vh - 130px);
    padding: 10px;
    resize: none;
    font-size: inherit;
    font-family: inherit;
}

.app-main-note-edit input {
    height: 50px;
    font-size: 2rem;
}


/* Preview */

.app-main-note-preview {
    border-top: 1px solid #ddd;
    overflow-y: scroll;
    background: rgba(0, 0, 0, 0.02);
}

.preview-title {
    padding: 25px 25px 0 25px;
    margin: 0;
}

.markdown-preview {
    padding: 0 25px 25px 25px;
    font-size: 1rem;
    line-height: 2rem;
}